*{
  padding: 0px;
  margin: 0px
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.navbar-container {
  height: 50px;
  width: 100%;
  margin-bottom: 20px;
  background-color: rgb(49, 134, 245);
}

.logout-button {
  position: absolute;
  top: 0;
  color: white;
  cursor: pointer;
  font-weight: bold;
  font-size: large;
  border: transparent;
  background-color: transparent;
  right: 30px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.home-button {
  position: absolute;
  top: 0;
  color: white;
  cursor: pointer;
  font-weight: bold;
  font-size: large;
  border: transparent;
  background-color: transparent;
  right: 130px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
